.Button {
    display: inline-block;
    padding: 10px 20px;
    border-radius: 4px;
    border: 1px solid #ccc;
    color:black;
    margin-right: 15px;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 12px;
}

.Button:focus {
    outline: none;
}

.Button:active {
    -webkit-box-shadow: inset 2px 2px 1px rgba(0,0,0, .3);
            box-shadow: inset 2px 2px 1px rgba(0,0,0, .3);
}

.Button:disabled {
    background: #ccc;
    color:black;
    cursor: not-allowed;
}

.error {
    color:rgb(240, 87, 108, 1)
}

.success {
    color:rgba(161,240,69,1)
}

.primary {
    background: #2884f6;
    color: #fff;
}




